<template>
  <section>
    <article class="card">
      <figure>
        <img
          :src="article.screen"
          :title="article.title"
          :alt="article.title"
        />
      </figure>
      <h2>{{ article.title }}</h2>
      <p>
        Feito com: <span>{{ article.stack }}</span>
      </p>
      <p>
        <a :href="article.url" target="_blank" class="btn btn-project"
          >Link do Projeto</a
        >
      </p>
      <p><a @click="labScroll()" class="btn btn-back">Voltar</a></p>
    </article>
  </section>
</template>

<script>
export default {
  name: "PortfolioDetalhe",

  created() {
    this.article = this.$route.params.data;
  },
  methods: {
    labScroll() {
      window.history.back();
      window.scrollTo({
        top: 0,
        behavior: "smooth",
      });
    },
  },
};
</script>

<style lang="scss" scoped>
$color-primary: #9e51ff;
$t-def: 0.3s;

section {
  grid-template-columns: repeat(3, 1fr);
  margin: 50px;
  article{
    grid-column: 2 / 3;
    background: #fff;
    text-align: center;
    h2 {
    font-size: 1.6em;
    color: #666;
    font-weight: lighter;
    margin-top: 15px;
  }
  @media(max-width: 350px){
    h2{
      font-size: 1.3em;
    }
  }
  p {
    color: rgba($color: #000000, $alpha: 0.7);
    font-size: 1.1em;
    margin: 15px 0;
  }
  .btn {
    border: 0px;
    border-radius: 50px;
    line-height: 40px;
    cursor: pointer;
    font-size: 0.7em;
    padding: 10px 20px;
    text-align: center;
    text-transform: uppercase;
    &:hover {
      text-decoration: none;
      animation-name: button_pulse;
      animation-duration: 2s;
      animation-iteration-count: infinite;
    }
  }
  span {
    color: #ff8141;
  }
  .btn-project {
    background: $color-primary;
    border: 1px solid $color-primary;
    color: #fff;
  }
  .btn-back {
    background: #fff;
    border: 1px solid $color-primary;
    color: $color-primary;
  }
  figure {
    img {
      width: 100%;
      // border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    }
  }
  }
}

@media(max-width: 1024px){
  section{
    grid-template-columns: 1fr;
    margin: 0px;
  }
}

// .card {
//   background: #eee;
// }

@keyframes button_pulse {
  0% {
    box-shadow: 0 0 0 0px rgba(0, 0, 0, 0.4);
  }
  100% {
    box-shadow: 0 0 0 20px rgba(0, 0, 0, 0);
  }
}
</style>